.worker-component{
  position: absolute;
  width: 80%;
  z-index: 120;
  background-color: white;
  padding: 20px;
  border: 1px solid #202041;
  border-radius: 10px;
  left: 8%;
  .title-info{
    width: 90%;
    display: flex;
    @media(max-width: 560px){
      width: 80%;
    }
    h3{
      width: 295px;
    }
  }
  @media(max-width: 991px){
    left: 15px;
  }
  button{
    margin: 10px 0;
    padding: 14px 36px;
    border: none;
    cursor: pointer;
    color:#ffffff;
    font-size: 16px;
    border-radius: 10px;
    background: linear-gradient(to right, #13b5f3, #3684d9);
    @media(max-width: 991px){
      padding: 14px;

    }

  }
  .workers{
    width: 100%;
  }
  .close {
    cursor: pointer;
    font-size: 25px;
    position: absolute;
    top: 18px;
    right: 0%;
    padding: 12px 16px;
    transform: translate(0%, -50%);
  }
}